import React from 'react';
import ReactApexCharts from 'react-apexcharts';

export const CheckExecucao = props => {
  const scores = Object.entries(props.squad).filter(entry => {
    return entry[0].includes('ScoreCE');
  });
  const categories = scores.map(entry => {
    return entry[0].replace('ScoreCE', '');
  });
  const data = scores.map(entry => {
    return entry[1];
  });
  return (
    <ReactApexCharts
      options={{
        chart: {
          height: '20%',
          type: 'radar',
          background: 'rgba(0, 0, 0, 0)'
        },
        plotOptions: {
          radar: {
            polygons: {
              strokeColor: '#ffffff',
              fill: {
                colors: ['#ffffff20']
              }
            }
          }
        },
        colors: ['#1c68b1'],
        fill: {
          colors: ['#1c68b1'],
          opacity: 0.9
        },
        markers: { size: 0 },
        stroke: {
          show: false
        },
        theme: {
          mode: 'dark',
          palette: 'palette8'
        },
        yaxis: {
          show: false
        },
        xaxis: {
          categories: categories,
          labels: {
            show: true,
            style: {
              colors: ['#ffffff'],
              fontSize: '1rem',
              fontFamily: 'Poppins'
            }
          }
        }
      }}
      series={[
        {
          name: '',
          data: data
        }
      ]}
      type="radar"
      height={600}
      width={600}
    />
  );
};

export default CheckExecucao;
